.form-search {
    width: 100%;
    max-width: 900px;
    padding: 1em 2em;
    background-color: #245B9E;
    margin: 1em auto;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

}

.search {
    width: 100%;
    max-width: 900px;
    margin: 1em auto;
    display: flex;
    justify-content: center;
    align-items: center;
}


.filters {
    display: flex;
}

.form-search .filters .item-form {
    width: 70%;
    margin-bottom: 1em;
    margin-top: 1em;
    display: flex;
    align-items: center;
}

.form-search .filters .item-form select {
    font-size: 18px;
    padding: .1em;
    border-radius: 8px;
    border: none;
    margin-left: 1em;
    width: 50%;
    border: 1px solid #ccc;
    font-family: Arial, sans-serif;
}

.form-search .filters .item-form select:focus {
    outline: none;
    border-color: #5b9dd9;
}

.form-search .filters .item-form select option {
  font-family: Arial, sans-serif;
  font-size: 16px;
  background-color: #fff;
  color: #000;
}


.form-search .filters .item-form label {
    font-weight: bold;
    color: white;
    font-size: 23px;
}

.search button {
    font-size: 40px;
    background-color: #245B9E;
    color: #ffffff;
    border: none;
    cursor: pointer;
}

.search input {
    padding: .6em;
    font-size: 20px;
    border: none;
    border-radius: 18px;
    margin-left: .5em;
    margin-right: .3em;
}

.search input:focus {
    border: none
}

.search h2 {
    color: #ffffff;
}

@media (max-width: 900px) {
    .search input {
        width: 30%;
        margin: 0;
    }
    .form-search {
        padding: .5em 0;
        margin: 1em 1em;
        display: flex;
        justify-content: space-around;
    }
    .form-search .filters .item-form label {
        font-size: 20px;
    }
    .form-search .filters .item-form select {
        font-size: 16px;
        margin-left: .3em;
    }
    .container-filters {
        margin: 0 .5em;
    }
}

.btn-filters {
    background-color: #ffffff;
    color: #245B9E;
    padding: 8px 16px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 1rem;
    transition: background-color 0.3s ease-in-out;
  }
  
 .btn-filters:hover {
    background-color: #245B9E;
    color: #fff;
    border: #ffffff 1px solid;
    padding: 7px 15px;
  }
  